import React, { useEffect, useState } from 'react';
import { useHistory, useParams } from 'react-router-dom';
import styled from 'styled-components';


let Box = styled.div`
    padding : 20px;
`;

let Title = styled.h4`
    font-size : 25px;
`;

function Detail(props){

    let show = true;

    useEffect(()=>{
        setTimeout(()=>{
            toggleAlert(false)
        }, 2000);
    }, [alert]);

    let [showAlert, toggleAlert] = useState(true);
    let [inputValue, changeInput] = useState("")

    let history = useHistory();
    let { id } = useParams();

    let productFound = props.shoeList.find((product)=>product.id==id)

    return(
      <div className="container">
        <Box>
            <Title className="red">Detail</Title>
        </Box>

        <input onChange={(e)=>{changeInput(e.target.value)}}></input>
        
        {
            inputValue
        }
        {
            showAlert? <Alert/> : null
        }
        <div className="row">
          <div className="col-md-6">
            <img src="https://codingapple1.github.io/shop/shoes1.jpg" width="100%" />
          </div>
          <div className="col-md-6 mt-4">
            <h4 className="pt-5">{productFound.title}</h4>
            <p>{productFound.content}</p>
            <p>{productFound.price}</p>
            <Info stock={props.stock}> </Info>
            <button className="btn btn-danger">주문하기</button> 
            <button className="btn btn-danger" onClick={()=>{
                history.goBack();
            }}>뒤로가기</button> 
          </div>
        </div>
  </div> 
    )
  }

function Alert(){
    return(
        <div className="my-alert">
            <p>Out of Stock!</p>
        </div>
    )
}

function Info(props){
  <p>재고 : {props.stock[0]}</p>
}

  export default Detail;